@import '../../../../styles/variables';

.allReviewsButton{
    width: 287px;
    height: 62px;
    background-color: $color-white;
    color: $color-default-text;
    @include flex-center-row;
    font-family: $font-title;
    font-weight: 600;
    font-size: 18px;
    border-radius: 15px;
    transition: $transition-style;

    &:hover{
        transform: scale(1.1);
    }
}