@import '../../../../styles/variables';

.aboutMePopUp{
    background: url(../../../../assets/images/pictures/aboutMePopUpBackground.png);
    background-color: $color-white;
    background-size: contain;
    background-position: top;
    background-repeat: no-repeat;
    left: 0; 
    top: 0; 
    height: 100vh;
    width: 560px;
    position: fixed;
    transform: none;
    z-index: 10001;

    .container{
        max-width: 480px;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 100%;
        padding-bottom: 20px;
        margin-top: 0;
        justify-content: space-between;
        .header{
            @include flex-between-row;
            position:relative;
            max-width: 470px;
            margin-top: 0;

            h5{
                color: $color-white;
                font-size: 14px;
                font-weight: 700;
            }
        }

        .aboutMePopUpBody{
            @include flex-center-column;
            align-items: center;
            max-height: 365px;
        }
    }
    

    .description{
        font-weight: 500;
        font-size: 16px;
        line-height: 25px;
    }

    .list{
        max-height: 300px;
        height: 100%;
        @include flex-between-column;
        align-items: start;
        .listTitle{
            font-weight: 800;
            font-size: 16px;
            line-height: 30px;
        }
        
        ul {
            list-style: disc;
            padding-left: 20px; 
            height: 100%;
            
            .item {
                font-size: 16px;
                font-weight: 500;
                line-height: 23px;
                
                &::marker {
                    color: $color-default-text; 
                }
            }

            .item + .item {
                margin-top: 5px;
            }
        }
    }
}