@import '../../../../styles/variables';

.marafonWorksMiniBanner{
    width: 207px;
    height: 187px;
    border-radius: 25px;
    background-color: $color-light-orange;
    display: flex;
    justify-content:space-evenly;
    align-items: end;
    flex-direction: row;
    position: relative;
    padding-bottom: 10px;
    .miniBannerImage{
        max-width: 89px;
        max-height: 89px;
        position: absolute;
        top: -30px;
        left: 15px;
    }
    h6{
        font-size: 16px;
        font-weight: 600;
        max-width: 171px;
        max-height: 66px;
        line-height: 22px;
    }
}