@import '../../../../styles/variables';

.formatBanner{
    background-color: $color-system-gray;
    border-radius: 20px;
    @include flex-center-column;
    
    .container{
        @include flex-between-column;
        align-items: start;
        justify-content: space-evenly;
        margin: 0 0;
    }

    .formatBannerTitle{
        max-width: 300px;
        font-size: 34px;
        font-weight: 900;
    }

    .formatBannerList{
        list-style: disc;
        padding-left: 20px;
        max-width: 300px;
        height: 175px;
        @include flex-between-column;
        align-items: start;
        .formatBannerListItem{
            font-size: 16px;
            font-weight: 500;
        }

        .formatBannerListItem + .formatBannerListItem{
            margin-top: 20px;
        }

        .formatBannerSubtitle{
            font-size: 12px;
            font-weight: 500;
            color: $color-dark-gray;
        }
    }
}